
.top { 
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 3;
}
.side {
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2; 
}
.body {
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 2;
    grid-column-end: 3;
}
.App
{
    display: grid;
    grid-template-columns: 250px auto;
    grid-template-rows: 50px auto;
    gap: 10px;
    width: 100vw;
    height: 100vh;
    background: #E2E2E2;
    margin: 0;
    padding: 0;
    


}